.card {
  background-size: cover;
  background-position: 50%;
  height: 170px;
  width: 170px;
  margin: 8px 25px;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  cursor: pointer;
}

.card>.img-container {
  overflow: hidden;
  text-align: center;
  justify-content: center; 
}

.card>.img-container>img {
  width: 170px;
}
